<template>
    <div v-if="isOnSettings">
        <h1>settings</h1>
    </div>
    <div v-else>
        <template v-if="weeklyMeals.length == 0">
            <div>
                <h3>You have yet to set things up!</h3>
                <router-link class="lead" to="/setup"
                    >Head to Settings</router-link
                >
                <p>
                    <em
                        >Make sure you at least have 1 staff meal morning, 1
                        staff meal evening, and 1 staff.</em
                    >
                </p>
            </div>
        </template>
        <template v-else>
            <div>
                <StaffMealTable :weeklyMeals="weeklyMeals" />
                <button
                    class="btn btn-primary"
                    @click="randomizeWeeklyStaffMeal"
                >
                    Randomize
                </button>
            </div>
        </template>
    </div>
</template>

<script>
import StaffMealTable from '@/components/StaffMealTable.vue'

import generateWeek from '@/composables/tablePopulator.js'

import data from '@/data/data.js'
import { ref } from '@vue/reactivity'
import { computed, onMounted } from '@vue/runtime-core'
import useStore from '../composables/store'
export default {
    name: 'App',
    components: {
        StaffMealTable,
    },
    setup() {
        // const { staffs, staffMealMorning, staffMealEvening } = data
        const store = useStore()
        store.initFromLocalStorage()

        const staffMealMorning = computed(() => store.state.staffMealMorning)
        const staffMealEvening = computed(() => store.state.staffMealEvening)
        const staffs = computed(() => store.state.staffs)

        const isOnSettings = ref(false)
        const weeklyMeals = ref(
            generateWeek(
                staffMealMorning.value,
                staffMealEvening.value,
                staffs.value
            )
        )

        const randomizeWeeklyStaffMeal = () => {
            weeklyMeals.value = generateWeek(
                staffMealMorning.value,
                staffMealEvening.value,
                staffs.value
            )
        }

        return {
            isOnSettings,
            weeklyMeals,
            randomizeWeeklyStaffMeal,
        }
    },
}
</script>

