import randomizer from './randomizer'
/*
[{day: 'Mon,am: {staff:, meal:, ingredients:},pm: {},},...]
*/

/**
 * Generates a randomized set of Meals given an array of Meals.
 * @param   {Array}   meals Array of Meals
 * @param   {Number}  size  Size of randomized array to create
 * @param   {Number}  iter  Iterations before repeats is allowed
 * @return  {Array}         Array of randomized Meals
 */
function getRandomizedMeals(meals, size, iter = 3) {
    // purpose is, I can add extra code here later
    // if changes need to be made
    return randomizer(meals, size, iter)
}

/**
 * Generates a randomized set of Staffs given an array of staffs.
 * @param  {Array}  staffs Array of Staffs
 * @param  {Number} size   Size of randomized array to create
 * @param  {Number} iter   Iterations before repeats is allowed
 * @return {Array}         Array of randomized Staffs
 */
function getRandomizedStaffs(staffs, size, iter = 3) {
    // same here
    return randomizer(staffs, size, iter)
}

/**
 * Creates a meal object for rendering in staffMealRow
 */
function mealFactory(meal, staff) {
    return {
        staff: staff,
        meal: meal.name,
        ingredients: meal.ingredients,
    }
}

/**
 * Creates a week worth of morning and evening meals.
 * @param {Array} morningMeals
 * @param {Array} eveningMeals
 */
export default function generateWeek(morningMeals, eveningMeals, staffs) {
    const DAYS = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']

    // return empty array if any of the array is empty
    if (
        morningMeals.length == 0 ||
        eveningMeals.length == 0 ||
        staffs.length == 0
    ) {
        return []
    }

    const randonmizedMorningMeals = getRandomizedMeals(morningMeals, 7)
    const randonmizedEveningMeals = getRandomizedMeals(eveningMeals, 7)
    const randomizedStaffs = getRandomizedStaffs(staffs, 14)

    let weeklyMeals = []

    for (const day of DAYS) {
        const am = mealFactory(
            randonmizedMorningMeals.shift(),
            randomizedStaffs.shift()
        )
        const pm = mealFactory(
            randonmizedEveningMeals.shift(),
            randomizedStaffs.shift()
        )

        const dayConstructor = {
            day,
            am,
            pm,
        }
        weeklyMeals.push(dayConstructor)
    }

    return weeklyMeals
}
