import { reactive, ref } from 'vue'

const LOCALSTORAGE_BASE_NAME = 'smr'
const defaultState = reactive({
    debug: true,
    staffMealMorning: [],
    staffMealEvening: [],
    staffs: [],
})

const fetchStates = function() {
    const staffMealMorning = getLocalStorage('_staffMealMorning')
    const staffMealEvening = getLocalStorage('_staffMealEvening')
    const staffs = getLocalStorage('_staffs')
    return {
        staffMealMorning,
        staffMealEvening,
        staffs,
    }
}

const getLocalStorage = function(name) {
    // return array or null
    const item = localStorage.getItem(LOCALSTORAGE_BASE_NAME + name)
    return JSON.parse(item)
}

const setLocalStorage = function(item, data) {
    localStorage.setItem(LOCALSTORAGE_BASE_NAME + item, JSON.stringify(data))
}

// TODO: Rename function
const emptyArrayOnNull = function(thing) {
    if (thing == null) {
        return []
    } else {
        return thing
    }
}

export default function useStore(state = defaultState) {
    /*
    const state = reactive({
        staffMealMorning: [],
        staffMealEvening: [],
        staffs: [],
    })
    */
    const actions = {
        initFromLocalStorage() {
            const { staffMealMorning, staffMealEvening, staffs } = fetchStates()
            // TODO: Refactor
            state.staffMealMorning = emptyArrayOnNull(staffMealMorning)
            state.staffMealEvening = emptyArrayOnNull(staffMealEvening)
            state.staffs = emptyArrayOnNull(staffs)
        },

        updateState(target, updState) {
            // can add debug here
            if (state.debug)
                console.log('before : ', state[state], 'after : ', updState)
            state[target] = updState
        },

        saveStates() {
            // save all current states
            setLocalStorage('_staffMealMorning', state.staffMealMorning)
            setLocalStorage('_staffMealEvening', state.staffMealEvening)
            setLocalStorage('_staffs', state.staffs)
        },
    }

    const store = {
        state,
        ...actions,
    }

    return {
        ...store,
    }
}
