<template>
    <form @submit.prevent="addStaffMeal">
        <table class="table">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Ingredients</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(meal, idx) in staffMeals" :key="idx">
                    <td>{{ meal.name }}</td>
                    <td>
                        <div class="ingredients">
                            <span>{{ meal.ingredients }}</span>
                            <button
                                class="btn btn-danger"
                                type="button"
                                style="margin-left: auto; padding: .188rem .33rem"
                                @click.prevent="deleteMeal(idx)"
                            >
                                <i class="fas fa-backspace"></i>
                            </button>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td>
                        <input
                            class="form-control"
                            type="text"
                            name="staffMealName"
                            id="staffMealName"
                            placeholder="Name"
                        />
                    </td>
                    <td>
                        <div class="input-group">
                            <input
                                class="form-control"
                                type="text"
                                name="staffMealIngredients"
                                id="staffMealIngredients"
                                placeholder="Ingredients"
                            />
                            <input
                                class="btn btn-success"
                                type="submit"
                                value="Add"
                            />
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
    </form>
</template>

<script>
import useStore from '@/composables/store.js'

export default {
    components: {},
    props: {
        staffMeals: Array,
        shift: String,
    },
    setup(props) {
        const store = useStore()

        const SHIFTS = {
            AM: 'am',
            PM: 'pm',
        }

        const list = {
            am: 'staffMealMorning',
            pm: 'staffMealEvening',
        }

        const addStaffMeal = function() {
            const formData = Object.fromEntries(new FormData(event.target))
            try {
                const meal = {
                    name: formData.staffMealName,
                    ingredients: formData.staffMealIngredients,
                    // ingredients: JSON.parse(formData.staffMealIngredients),
                }
                store.state[list[props.shift]].push(meal)
                // save the state
                store.saveStates()
            } catch (err) {
                console.log(err)
                return
            }
        }

        const deleteMeal = function(idx) {
            // fancy way of getting staffMealMorning vs staffMealEvening 
            // programmatically
            store.state[list[props.shift]].splice(idx, 1)
            store.saveStates()
        }
        return { addStaffMeal, deleteMeal }
    },
}
</script>

<style scoped>
td > input {
    width: 100%;
}

.ingredients {
    display: flex;
    width: 100%;
}
</style>
