function generateRandomIndex(length) {
    return parseInt(Math.random() * length)
}

export default function randomizer(array, size, iter = 3) {
    // generates a neatly randomized array of size
    // each repeats can only happens after it's iter size apart
    const ITERATION_DIFF = iter
    const arrayLength = array.length

    let updArray = []
    let alreadyExists = []
    for (let i = 0; i < size; i++) {
        let item = array[generateRandomIndex(arrayLength)]

        // reshuffle until item doesn't exists in alreadyExists
        // if the array is bigger than iteration size by 1
        while (
            alreadyExists.includes(item) &&
            arrayLength - ITERATION_DIFF > 1
        ) {
            const randomIndex = generateRandomIndex(arrayLength)
            item = array[randomIndex]
        }

        updArray.push(item)
        alreadyExists.push(item)

        // remove the item once it is 2 iterations old
        if (i > ITERATION_DIFF) alreadyExists.shift()
    }

    return updArray
}
