<template>
    <h1>Setup</h1>
    <nav>
        <div class="nav nav-tabs" id="nav-tab" role="tablist">
            <button
                class="nav-link active text-light bg-dark"
                id="nav-home-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-home"
                type="button"
                role="tab"
                aria-controls="nav-home"
                aria-selected="true"
            >
                Morning Menus
            </button>
            <button
                class="nav-link text-light bg-dark"
                id="nav-profile-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-profile"
                type="button"
                role="tab"
                aria-controls="nav-profile"
                aria-selected="false"
            >
                Evening Menus
            </button>
            <button
                class="nav-link text-light bg-dark"
                id="nav-contact-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-contact"
                type="button"
                role="tab"
                aria-controls="nav-contact"
                aria-selected="false"
            >
                Staffs
            </button>
        </div>
    </nav>
    <div class="tab-content" id="nav-tabContent">
        <div
            class="tab-pane fade show active"
            id="nav-home"
            role="tabpanel"
            aria-labelledby="nav-home-tab"
        >
            <SetupMenus :staffMeals="staffMealMorning" :shift="SHIFT.AM" />
        </div>
        <div
            class="tab-pane fade"
            id="nav-profile"
            role="tabpanel"
            aria-labelledby="nav-profile-tab"
        >
            <SetupMenus :staffMeals="staffMealEvening" :shift="SHIFT.PM" />
        </div>
        <div
            class="tab-pane fade"
            id="nav-contact"
            role="tabpanel"
            aria-labelledby="nav-contact-tab"
        >
            <SetupStaffs :staffs="staffs" />
        </div>
    </div>
</template>

<script>
import SetupMenus from '@/components/SetupMenus.vue'
import SetupStaffs from '@/components/SetupStaffs.vue'

import useStore from '../composables/store'
import { computed } from '@vue/runtime-core'

export default {
    components: {
        SetupMenus,
        SetupStaffs,
    },
    setup() {
        const store = useStore()
        // init states
        store.initFromLocalStorage()
        const SHIFT = {
            AM: 'am',
            PM: 'pm',
        }
        const staffMealMorning = computed(() => store.state.staffMealMorning)
        const staffMealEvening = computed(() => store.state.staffMealEvening)
        const staffs = computed(() => store.state.staffs)

        return { staffMealMorning, staffMealEvening, staffs, SHIFT }
    },
}
</script>

