<template>
    <!-- <table class="table">
        <thead>
            <tr>
                <th>Staff</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(staff, idx) in staffs" :key="idx">
                <td>{{ staff }}</td>
            </tr>
        </tbody>
    </table> -->
    <div class="col-4">
        <h3>Staffs</h3>
        <ul class="list-group">
            <li
                class="list-group-item bg-dark text-light"
                v-for="(staff, idx) in staffs"
                :key="idx"
            >
                <div style="display: flex;">
                    {{ idx + 1 }}. {{ staff }}
                    <button
                        class="btn btn-danger"
                        type="button"
                        style="margin-left: auto; padding: .188rem .33rem"
                        @click.prevent="deleteStaff(idx)"
                    >
                        <i class="fas fa-backspace"></i>
                    </button>
                </div>
            </li>
        </ul>
    </div>
    <div class="col-4">
        <label class="form-label" for="new-staff">Add New Staff</label>
        <div class="input-group">
            <input
                class="form-control"
                type="text"
                id="new-staff"
                placeholder="Name"
                v-model="staff"
            />
            <button class="btn btn-success" @click="addStaff">Add</button>
        </div>
    </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import useStore from '../composables/store'
export default {
    name: 'SetupTable',
    props: {
        staffs: Array,
    },
    setup(props) {
        const store = useStore()

        const staff = ref(null)

        const addStaff = function() {
            store.state.staffs.push(staff.value)
            store.saveStates()
        }

        const deleteStaff = function(idx) {
            store.state.staffs.splice(idx, 1)
            store.saveStates()
        }
        return {
            staff,
            addStaff,
            deleteStaff,
        }
    },
}
</script>

